/* ====================== */
/* Settings.css - Revised */
/* ====================== */

/* ========== */
/* Variables */
/* ========== */
:root {
  --primary-color: #007bff;
  --primary-hover: #0056b3;
  --success-color: #28a745;
  --success-hover: #1e7e34;
  --text-color: #333;
  --text-light: #555;
  --text-lighter: #777;
  --border-color: #ccc;
  --border-light: #e0e0e0;
  --bg-light: #f8f9fa;
  --bg-lighter: #f9f9f9;
  --bg-gray: #e9e9e9;
  --bg-gray-hover: #d4d4d4;
  --shadow-light: 0 2px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.2s ease;
}

/* ========== */
/* Base Style */
/* ========== */
body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

h2, h3 {
  color: var(--text-color);
  margin-top: 0;
}

h2 {
  margin-bottom: 10px;
  text-align: left;
}

h3 {
  margin-bottom: 15px;
}

/* ============ */
/* Layout      */
/* ============ */
.layout-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.right-side-settings {
  flex-grow: 1;
  background-color: #fff;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* ============ */
/* Sidebar      */
/* ============ */
.left-side-settings {
  width: 95px;
  background-color: #5B86E5;
  font-size: 14px;
  border-radius: 0 10px 10px 0;
  box-shadow: 2px 0 5px #789ef0;
  margin-right: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  flex-shrink: 0;
  overflow: hidden;
}

.sidebar-icon-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  margin-bottom: 5px;
  cursor: pointer;
  color: white;
  transition: var(--transition);
}

.sidebar-icon-item:hover {
  background-color: rgb(114, 154, 240);
  transform: scale(1.05);
}

.sidebar-icon-item svg {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: white;
}

.sidebar-icon-item span {
  font-size: 0.9em;
  color: white;
}

/* ============ */
/* Go Back Button */
/* ============ */
.goback-button {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #fff;
  border: 1px solid var(--border-color);
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  gap: 6px;
  box-shadow: var(--shadow-light);
  transition: var(--transition);
  z-index: 10;
}

.goback-button:hover {
  background-color: #f0f0f0;
  transform: scale(1.05);
}

.arrow {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-top: 2px solid #000;
  border-right: 2px solid #000;
}

.arrow-left {
  transform: rotate(-135deg);
}

/* ============ */
/* Forms        */
/* ============ */
.info-row {
  display: grid;
  grid-template-columns: 180px auto;
  margin-bottom: 10px;
  align-items: center;
}

label {
  font-weight: bold;
  color: var(--text-light);
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
input[type="tel"],
select,
textarea {
  padding: 8px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  box-sizing: border-box;
  cursor: pointer;
  transition: var(--transition);
}

input[type="text"],
input[type="date"],
input[type="tel"],
select,
textarea {
  width: calc(100% - 190px);
}

select {
  width: 200px;
}

textarea {
  min-height: 80px;
}

input[type="checkbox"] {
  width: auto;
  margin-left: 0;
}

/* ============ */
/* Buttons      */
/* ============ */
button {
  padding: 10px 15px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
  transition: var(--transition);
}

.save-profile-button {
  background-color: var(--success-color);
}

.save-profile-button:hover {
  background-color: var(--success-hover);
}

.settings-content > button,
.save-button {
  background-color: var(--primary-color);
}

.settings-content > button:hover,
.save-button:hover {
  background-color: var(--primary-hover);
}

/* ============ */
/* Sections     */
/* ============ */
.settings-section {
  margin-bottom: 30px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: var(--bg-lighter);
}

/* ============ */
/* Datepicker   */
/* ============ */
.react-datepicker__header {
  background-color: var(--bg-light);
  border-bottom: 1px solid var(--border-light);
  padding: 12px 0;
  text-align: center;
  position: relative;
}

.react-datepicker__current-month {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-color);
  margin: 0 auto 8px;
  width: 100%;
  padding: 0 40px;
  text-align: center;
}

.react-datepicker__month-dropdown-container,
.react-datepicker__year-dropdown-container {
  position: relative;
  display: inline-block;
}

.react-datepicker__month-read-view:hover,
.react-datepicker__year-read-view:hover,
.react-datepicker__month-option:hover,
.react-datepicker__year-option:hover {
  background-color: var(--bg-gray);
}

.react-datepicker__month-dropdown,
.react-datepicker__year-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  background: white;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.15);
  padding: 8px;
  max-height: 200px;
  overflow-y: auto;
}

.react-datepicker__navigation {
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
  position: absolute;
  top: 12px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: var(--transition);
}

.react-datepicker__navigation:hover {
  background-color: var(--bg-gray);
}

.react-datepicker__navigation--previous {
  left: 10px;
}

.react-datepicker__navigation--next {
  right: 10px;
}

.react-datepicker__day-names {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
  padding: 0 10px;
}

.react-datepicker__day-name {
  color: var(--text-light);
  font-size: 0.9rem;
  font-weight: 500;
  width: 36px;
  text-align: center;
}

/* ===================== */
/* Mobile Responsiveness */
/* ===================== */
@media (max-width: 768px) {
  .layout-container {
    flex-direction: column;
  }

  .right-side-settings {
    padding: 10px;
  }

  .settings-section {
    margin-bottom: 20px;
  }

  .info-row {
    grid-template-columns: 1fr;
    gap: 5px;
  }

  input[type="text"],
  input[type="date"],
  input[type="tel"],
  select,
  textarea {
    width: 100%;
  }
}

/* ======================== */
/* App-Specific Custom Rows */
/* ======================== */
.app-settings .info-row {
  display: flex;
  align-items: center;
}

.app-settings .info-row > div {
  display: flex;
  align-items: center;
  width: calc(100% - 190px);
}

.app-settings .info-row > div label {
  font-weight: normal;
  margin-right: 10px;
}

.date-picker-input {
  padding: 8px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  box-sizing: border-box;
  flex-grow: 1;
  cursor: pointer;
}
